import React, { Component } from "react";
import { getUserInfo, deleteUserInfo,updateUserInfo } from "../../api/api";
import { MDBDataTable, MDBIcon } from "mdbreact";
import EditUserModal from "../../common/modal/EditUserModal";
import Navigation from "../../common/navbar/Navigation";
import Swal from "sweetalert2";

class Aboutus extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      finalData: [],
      modalShow: false,
      id: ""
    };
    // this.getUserData=this.getUserData.bind(this);
  }

   updateHandler=async (id,value)=>{
   console.log("result--------------->",id,value)
    try {
      let result = await updateUserInfo(id,value);
      console.log("resut----->", result);
      if (result) {
      this.getUserData();
        Swal.fire({
          title: "User Updated Successful",
          text: "Updating page",
          icon: "success",
          timer: 1000
        });
      }
    } catch (error) {
      console.log("erooorrrrr----------->",error);
      Swal.fire({
        title: "Unsucessful User  Updating ",
        text: "Please check all the fileds",
        icon: "warning",
        timer: 1000
      });
    }

  }
  deleteUser=(id)=> {
    Swal.fire({
      title: "Are you sure want to delete ?",
      text: "This  will delete current user..",
      icon: "warning",
      showCancelButton: true
    }).then(async(result) => {
      if (result.value) {
        try{
          let result = await deleteUserInfo(id);
          if (result) {
            this.getUserData();
            Swal.fire({
              title: "Your file get Deleted",
              icon: "success",
              timer: 1000
            });
          }
        }catch(error){
            console.log(error);
        }
      
      }
    });
  }


    getUserData =async ()=>{
    let result = await getUserInfo();
    console.log("dhsuyfgsuydfguysdgfuydsfg",result);
  let tempData = result.data.map((item, i) => {
    return {
      ...item,
      edit: (
        <button
          onClick={() => this.setState({ modalShow: true, id: i })}
          className="btn btn-dark btn-sm"
        >
          Edit
        </button>
      ),
      delete: (
        <button
          onClick={() => this.deleteUser(item._id)}
          className="btn btn-danger btn-sm"
        >
          Delete
        </button>
      )
    };
  });
  console.log("main", tempData);

  if (result) {
    const data = {
      columns: [
        {
          label: "Id",
          field: "_id",
          sort: "asc",
          width: 100
        },
        {
          label: "Email",
          field: "email",
          sort: "asc",
          width: 250
        },
        {
          label: "Firstname",
          field: "first_name",
          sort: "asc",
          width: 200
        },
        {
          label: "Lastname",
          field: "last_name",
          sort: "asc",
          width: 100
        },
        {
          label: "Edit",
          field: "edit",
          sort: "asc",
          width: 100
        },
        {
          label: "Delete",
          field: "delete",
          sort: "asc",
          width: 100
        }
      ],
      rows: tempData
    };

    this.setState({ finalData: data });
  }
    
  }
   componentDidMount() {
    this.getUserData();
  }
  render() {
    return (
      <div>
        <Navigation />
        <div style={{ backgroundColor: "pink" }}>
        
          <MDBDataTable striped bordered hover data={this.state.finalData} />
        </div>
        <EditUserModal
          id={this.state.id}
          data={this.state.finalData}
          show={this.state.modalShow}
          onHide={() => this.setState({ modalShow: false })}
          onUpdatedata={this.updateHandler}
        />
      </div>
    );
  }
  
}

export default Aboutus;
