import { Modal, Button } from "react-bootstrap";
import React, { Component } from "react";

class EditUserModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      first_name: "",
      last_name: "",
      email: ""
    };
  }
 
  updateHandler() {
    let id;
    id=this.props.data.rows[this.props.id]._id;
   
    if(this.state.first_name===""){
      this.setState({
        first_name:this.props.data.rows[this.props.id].first_name
      })
    }
     else if(this.state.last_name===""){
      this.setState({
        last_name:this.props.data.rows[this.props.id].last_name
      });
      
    }
     else if(this.state.email===""){
      this.setState({
        email:this.props.data.rows[this.props.id].email
      });
      
    }
    else{
      this.props.onUpdatedata(id,this.state);
      this.props.onHide();
    }
    console.log("state -------------------------- state",this.state);
    // this.setState({
    //   first_name: "",
    //   last_name: "",
    //   email: ""
    // });
  }

  onChangeHandler(event) {
    event.preventDefault();
    let { id, value } = event.target;
    this.setState({[id]: value});
  }
  render() {
    const id = this.props.id;
    return (
      <Modal
        {...this.props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        {console.log("props are here........", this.props)}
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Edit user
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="container">
            <div className="row w-50 m-auto p-1">
              <div className="col-4">
                <label>Id</label>
              </div>
              <div className="col-8">
                <input
                  className="form-control"
                  id="id"
                  value={id === "" ? null : this.props.data.rows[id]._id}
                  type="text"
                  readOnly
                />
              </div>
            </div>
            <div className="row w-50 m-auto p-1">
              <div className="col-4">
                <label>Email</label>
              </div>
              <div className="col-8">
                <input
                  className="form-control"
                  id="email"
                  // value={id === "" ? null : this.state.email}
                  defaultValue={id === "" ? null : this.props.data.rows[id].email}
                  type="text"
                  onChange={this.onChangeHandler.bind(this)}
                />
              </div>
            </div>
            <div className="row w-50 m-auto p-1">
              <div className="col-4">
                <label>Firstname</label>
              </div>
              <div className="col-8">
                <input
                  className="form-control"
                  id="first_name"
                  //value={id === "" ? null : this.props.data.rows[id].first_name}
                  defaultValue={id === "" ? null : this.props.data.rows[id].first_name}
                  type="text"
                  onChange={this.onChangeHandler.bind(this)}
                />
              </div>
            </div>
            <div className="row w-50 m-auto p-1">
              <div className="col-4">
                <label>Lastname</label>
              </div>
              <div className="col-8">
                <input
                  className="form-control"
                  id="last_name"
                 // value={id === "" ? null : this.props.data.rows[id].last_name}
                  defaultValue={id === "" ? null : this.props.data.rows[id].last_name}
                  type="text"
                  onChange={this.onChangeHandler.bind(this)}
                />
              </div>
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button className="btn btn-danger" onClick={this.props.onHide}>
            Close
          </Button>
          <Button className="btn btn-success" onClick={this.updateHandler.bind(this)}>
            Update
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}

export default EditUserModal;

